<template>
  <div class="sendHouse">
    <navigation-bar  @onLeftClick="goBack" pageName="选择城市" />
    <div class="box search-box">
      <van-index-bar highlight-color="#fb5768" style="height:100%;overflow:scroll;">
        <div class="current_city" @click="selectCurrent">
          <img :src="positionIcon" alt="">
          <div>当前所在城市：{{location}}</div>
        </div>
        <div v-for="(value, name) in cityList" :key="name">
          <van-index-anchor :index="name"/>
          <div class="list">
            <div v-for="(city, index) in value" :key="index" class="city" @click="selectCity(city)">
              {{city.CITY_NAME}}
            </div>
          </div>
        </div>
      </van-index-bar>
    </div>
  </div>
</template>

<script>
import { getCityListService } from "../../../services/houseRentalService";
import positionIcon from "@/assets/images/home/houseRental/place.png";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "houseCity",
  data() {
    return {
      cityList: {},
      positionIcon: positionIcon
    };
  },

  created() {
    this.location = this.$userInfo && this.$userInfo.CITY_NAME ? this.$userInfo.CITY_NAME : "青岛市";
    this.getCityList();
  },
  mounted () {
    setTimeout(function() {
      document.querySelector(".search-box").append(document.querySelector(".van-index-bar__sidebar"));
    }, 100)
  },
  components: {
    NavigationBar
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      this.$router.go(-1);
    },

    /**
     * 选择当前城市
     */
    selectCurrent() {
      this.selectCity({
        CITY_NAME: this.location,
        CITY_CODE: ''
      })
    },

    /**
     * 选择城市
     */
    selectCity(item) {
      this.$store.dispatch('setCity', {
        cityName: item.CITY_NAME,
        cityCode: item.CITY_CODE
      })
      this.$store.dispatch('setAreaName', "");
      this.$store.dispatch('setReload', true);
      this.goBack();
    },

    /**
     * 获取城市列表
     */
    getCityList() {
      getCityListService().then(
        result => {
          this.setCityList(result);
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 设置城市列表
     */
    setCityList(list) {
      var reg = /^[A-Za-z]+$/;
      var tmp = {};
      for (var i = 0; i < list.length; i++) {
        var pingyin = list[i].CITY_NAME_PINYIN;
        if (pingyin && pingyin != undefined && pingyin.length > 0) {
          var letterStr = reg.test(pingyin[0]) ? pingyin[0].toUpperCase() : "#";
        } else {
          letterStr = "#";
        }
        if (tmp[letterStr] == undefined) {
          tmp[letterStr] = [];
        }
        tmp[letterStr].push(list[i]);
      }
      this.cityList = tmp;
    }
  }
};
</script>
<style scoped lang="scss">
.sendHouse {
  width: 100%;
  height: 100%;
  // position: relative;
  background: #f4f4f4;
  display: flex;
  flex-direction: column;
  .current_city {
    line-height: 44px;
    border-top: 12px solid #f4f4f4;
    border-bottom: 12px solid #f4f4f4;
    background: #ffffff;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 14px;
    img {
      width: 15px;
      height: 18px;
      margin: 0 5px;
    }
  }
  .box {
    flex: 1;
    width: 100%;
    overflow: hidden;
  }
  .list {
    padding-right: 24px;
    .city {
      border-color: #ddd;
      background-color: #fff;
      color: #444;
      padding: 16px;
      border-right: 1px solid #ddd;
      border-top: 1px solid #ddd;
      font-size: 14px;
    }
  }
  /deep/.van-index-anchor {
    padding: 8px;
    background-color: #f5f5f5;
    font-size: 14px;
    height: 30px;
    line-height: 30px;
    border-right: 1px solid #ddd;
    border-top: 1px solid #ddd;
    margin-right: 24px;
  }
  // 右侧字母
  /deep/.van-index-bar__sidebar {
    width: 24px;
  }
  /deep/.van-index-bar__index {
    display: block;
    line-height: 20px;
    height: 20px;
    color: #fb5768;
    padding: 0;
    width: 24px;
    text-align: center;
  }
}
</style>
